.meme-generator-component{
    width: 100%;
    height: 100vh;
    background-color: #29577E;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.meme-generator-heading{
    color: #FDFFFC;
    margin-top: 1.5rem; 
    text-align: center;
}

.meme-generator-heading h1{
    font-size: 3.25rem;
    letter-spacing: 3px;
    margin-top: 0;
    margin-bottom: 10px;
}

.meme-generator-heading h2{
   font-size: 1.5rem;
}

.meme-generator-form{
    width: 90%;
    position: relative;
    top: 5%;
}

.form-group{
    margin-bottom: 1.5rem;
}

.form-label{
    font-size: 1rem;
    color: #FDFFFC;
}

.btn-container{
    width: 50%;
    margin: 0 auto;
    margin-top: 2.5rem;
}

.btn{
    width: 100%;
    background-color: #FD5200;
    border-radius: 5px;
    color: #fff;
    position: relative;
    font-weight: 700;
    font-size: 1.25rem;
    margin: 0 auto;
    -webkit-transition: all 2s;
    -o-transition: all 2s;
    transition: all 2s;
}

.btn:hover{
    top: 2px;
}


